import React, { useEffect, useState } from "react";
import "./sass/style.scss";
import {
  BrowserRouter as Router,
  HashRouter,
  Routes,
  Route,
} from "react-router-dom";

import Web3 from "web3";
import LotteryJson from "./abis/LotteryContract.json";

import { ToastContainer } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";
import "aos/dist/aos.css";
import AOS from "aos";
import { toast } from "react-toastify";

// Pages
import Home from "./pages/Home";
import FAQ from "./pages/FAQ";
import History from "./pages/History";
import HowToPlay from "./pages/HowToPlay";
import Referrals from "./pages/Referrals";
import PrivacyPolicy from "./pages/PrivacyPolicy";
import SmartContract from "./pages/SmartContract";
import SupportEmail from "./pages/SupportEmail";
import TermsOfServices from "./pages/TermsOfServices";
import NotFound from "./pages/NotFound"; // not found 404 page

import { AppContext } from "./AppContext";



const App = () => {
  const [connected, setConnected] = useState(false);
  const [account, setAccount] = useState("");
  const [gameStartDate, setGameStartDate] = useState(0);
  const [lotteryContract, setLotteryContract] = useState(null);
  const [loading, setLoading] = useState(true);
  const [oneTicketPrice, setOneTicketPrice] = useState(1);

  const ConnectAccount = async () => {
    await loadWeb3();
  };

  // toast.success("Metamask Connected");
  // toast.error("");
  // toast.info("You need to install Metamask extension"

  const loadWeb3 = async () => {
    if (window.ethereum) {
      window.web3 = new Web3(window.ethereum);
      await window.ethereum.enable();
      await loadBlockChatData();

    } else if (window.web3) {
      window.web3 = new Web3(window.web3.currentProvider);
      await loadBlockChatData();

    } else {
      toast.info("You need to install Metamask extension");
    }
  };



  const loadBlockChatData = async () => {
    const web3 = window.web3;
    const accounts = await web3.eth.getAccounts();
    web3.eth.defaultAccount = accounts[0];
    setAccount(accounts[0]);

    const networkId = await web3.eth.net.getId();

    // 108 for thundercare main  18 for polygon mainnet
    if (networkId === 108 || networkId === 18) {
      const networkAddress = "0x89871efF35a33274847Ee0af264E7053121794f8";
      const lotteryContract = await new web3.eth.Contract(
        LotteryJson.abi,
        networkAddress
      );
      setLotteryContract(lotteryContract);
      setConnected(true);

      const gameStartDate = await lotteryContract.methods
        .getGameStartDate()
        .call();
      setGameStartDate(gameStartDate.toString());


      const oneTicketPrice = await lotteryContract.methods
        .getOneTicketPrice()
        .call();
      setOneTicketPrice(oneTicketPrice.toString() / 10 ** 18);

      setLoading(false);
    } else {
      // window.alert("Blockchat contract not deployed to detected network. Please connect with polygon matic");
      toast.info(
        "Blockchat contract not deployed to this network. Please connect with ThunderCore"
      );
      // switchToThundercoreMainnet();
      switchToThundercoreTestNet();
    }
  };

  const switchToThundercoreTestNet = async () => {
    try {
      await window.ethereum.request({
        method: "wallet_switchEthereumChain",
        params: [{ chainId: "0x12" }],
      });
      loadBlockChatData();
    } catch (error) {
      if (error.code === 4902) {
        try {
          await window.ethereum.request({
            method: "wallet_addEthereumChain",
            params: [
              {
                chainId: "0x12",
                chainName: "TestNet ThunderCore",
                rpcUrls: ["https://testnet-rpc.thundercore.com"],
                nativeCurrency: {
                  name: "ThunderCore",
                  symbol: "TT",
                  decimals: 18,
                },
                blockExplorerUrls: ["https://explorer-mumbai.maticvigil.com"],
              },
            ],
          });
          loadBlockChatData();

        } catch (error) {
          toast.error(error.message);
        }
      }
    }
  }


  const switchToThundercoreMainnet = async () => {
    try {
      await window.ethereum.request({
        method: "wallet_switchEthereumChain",
        params: [{ chainId: "0x6c" }],
      });
      loadBlockChatData();
    } catch (error) {
      if (error.code === 4902) {
        try {
          await window.ethereum.request({
            method: "wallet_addEthereumChain",
            params: [
              {
                chainId: "0x6c",
                chainName: "ThunderCore Mainnet",
                rpcUrls: ["https://mainnet-rpc.thundercore.com"],
                nativeCurrency: {
                  name: "ThunderCore",
                  symbol: "TT",
                  decimals: 18,
                },
                blockExplorerUrls: ["https://scan.thundercore.com/"],
              },
            ],
          });
          loadBlockChatData();

        } catch (error) {
          toast.error(error.message);
        }
      }
    }
  }



  useEffect(() => {
    // setGameStartDate(1637400061);
    AOS.init({
      duration: 1000,
    });
  }, []);

  return (
    <>
      <ToastContainer />
      <HashRouter>
        <AppContext.Provider
          value={{ connected, account, loading, ConnectAccount }}
        >
          <Routes>
            <Route
              exact
              path="/"
              element={
                <Home
                  gameStartDate={gameStartDate}
                  lotteryContract={lotteryContract}
                  oneTicketPrice={oneTicketPrice}
                />
              }
            />
            <Route
              exact
              path="/refferal/:refferalCode"
              element={
                <Home
                  gameStartDate={gameStartDate}
                  lotteryContract={lotteryContract}
                  oneTicketPrice={oneTicketPrice}
                />
              }
            />
            <Route exact path="/faq" element={<FAQ />} />
            <Route
              exact
              path="/history"
              element={<History lotteryContract={lotteryContract} />}
            />
            <Route exact path="/how-to-play" element={<HowToPlay />} />
            <Route exact path="/referrals" element={<Referrals />} />
            <Route exact path="/privacy-policy" element={<PrivacyPolicy />} />
            <Route exact path="/smart-contract" element={<SmartContract />} />
            <Route exact path="/support-email" element={<SupportEmail />} />
            <Route
              exact
              path="/terms-of-services"
              element={<TermsOfServices />}
            />
            <Route path="*" element={<NotFound />} />
          </Routes>
        </AppContext.Provider>
      </HashRouter>
    </>
  );
};

export default App;
