import React, { useContext, useState, useEffect } from "react";
// import Components
import Header from "../layout/Header";
import Navbar from "../layout/Navbar";

import Button from "../components/Button";
import Counter from "../components/Counter";
import FirstRound from "../components/FirstRound";
import Tickets from "../components/Tickets";
import Wallets from "../components/modals/Wallets";

import { AppContext } from "../AppContext";
import { useParams } from "react-router";

const Home = (props) => {
  const { lotteryContract, gameStartDate, oneTicketPrice } = props;
  const { connected, account, loading, ConnectAccount } = useContext(AppContext);
  const [modal, setModal] = useState(false);

  const [mainLoading, setMainLoading] = useState(true);
  const [ticketLeft, setTicketLeft] = useState(null);
  const [ticketInDraw, setTicketInDraw] = useState(null);
  const [canPlayerBuyTicket, setCanPlayerBuyTicket] = useState(false);
  const [currentGameCount, setCurrentGameCount] = useState(null);
  const [playerTickets, setPlayerTickets] = useState(0);

  const { refferalCode } = useParams();
  // const [isRefferal, setIsRefferal] = useState(false);
  const [playerRefferalCode, setPlayerRefferalCode] = useState(null);

  var RefferalLink = document.location + "refferal/" + account;

  useEffect(() => {
    if (connected) {
      checkRefferal();
      loadTicketsData();
    }
  }, []);

  useEffect(() => {
    if (connected) {
      loadTicketsData();
      checkRefferal();
    }
  }, [lotteryContract]);

  useEffect(() => {
    if (connected) {
      loadTicketsData();
      checkRefferal();
    }
  }, [connected]);


  const checkRefferal = () => {
    if (refferalCode != null) {
      setPlayerRefferalCode(refferalCode)
      RefferalLink = document.location + "refferal/" + account;

    } else {
    }
  }

  const connectToAccount = () => {
    if(!connected){
      ConnectAccount();
    }
  }

  const autoRefreh = () => {
    if (!mainLoading && connected) {
      AutoloadTicketsData();
      checkRefferal();
    }
  }

  setInterval(autoRefreh, 2000);

  const loadTicketsData = async () => {
    setMainLoading(true);
    const getCanPlayerBuyTicket = await lotteryContract.methods.getCanPlayerBuyTicket().call();

    const getCurrentGameCount = await lotteryContract.methods.getCurrentGameCount().call();

    const getTotalTickets = await lotteryContract.methods.getTotalTickets().call();
    const getOnlySoldTicketOfGame = await lotteryContract.methods.getOnlySoldTicketOfGame(getCurrentGameCount.toString()).call();

    const getTotalTicketOfGame = await lotteryContract.methods.getTotalTicketOfGame(getCurrentGameCount.toString()).call();

    const getAllTicketsOfUserInGame = await lotteryContract.methods.getAllTicketsOfUserInGame(account, getCurrentGameCount.toString()).call();

    setCanPlayerBuyTicket(getCanPlayerBuyTicket);
    setCurrentGameCount(getCurrentGameCount.toString());
    setTicketLeft(getTotalTickets.toString() - getOnlySoldTicketOfGame.toString());
    setTicketInDraw(getTotalTicketOfGame.toString());
    setPlayerTickets(getAllTicketsOfUserInGame.length);

    setMainLoading(false);
  }

  const AutoloadTicketsData = async () => {
    if (!mainLoading) {

      const getCanPlayerBuyTicket = await lotteryContract.methods.getCanPlayerBuyTicket().call();

      const getCurrentGameCount = await lotteryContract.methods.getCurrentGameCount().call();

      const getTotalTickets = await lotteryContract.methods.getTotalTickets().call();
      const getOnlySoldTicketOfGame = await lotteryContract.methods.getOnlySoldTicketOfGame(getCurrentGameCount.toString()).call();

      const getTotalTicketOfGame = await lotteryContract.methods.getTotalTicketOfGame(getCurrentGameCount.toString()).call();

      const getAllTicketsOfUserInGame = await lotteryContract.methods.getAllTicketsOfUserInGame(account, getCurrentGameCount.toString()).call();

      setCanPlayerBuyTicket(getCanPlayerBuyTicket);
      setCurrentGameCount(getCurrentGameCount.toString());
      setTicketLeft(getTotalTickets.toString() - getOnlySoldTicketOfGame.toString());
      setTicketInDraw(getTotalTicketOfGame.toString());
      setPlayerTickets(getAllTicketsOfUserInGame.length);
    }
  }

  const buyTicket = async () => {
    if (connected && lotteryContract != null) {
      setMainLoading(true);
      const getCanPlayerBuyTicket = await lotteryContract.methods.getCanPlayerBuyTicket().call();
      if (getCanPlayerBuyTicket) {
        if (playerRefferalCode != null) {
          buyTicketwithRefferalCode();
        } else {
          buyTicketWithoutRefferal()
        }
      } else {
        alert('game not started yet')
      }
    } else {
      setMainLoading(false);
      connectToAccount();
      // buyTicket();
    }
  }
  const buyTicketWithoutRefferal = async () => {
    await lotteryContract.methods.buyTicket()
      .send({ from: account, value: oneTicketPrice * 10 ** 18 })
      .on('confirmation', (confirmationNumber, receipt) => {
        loadTicketsData();
      })
      .on('error', function (error, receipt) {
        loadTicketsData();
      });
  }

  const buyTicketwithRefferalCode = async () => {
    await lotteryContract.methods.buyTicketUsingReferral(playerRefferalCode)
      .send({ from: account, value: oneTicketPrice * 10 ** 18 })
      .on('confirmation', (confirmationNumber, receipt) => {
        loadTicketsData();
      })
      .on('error', function (error, receipt) {
        loadTicketsData();
      });
  }

  return (
    <>
      <Header />
      <Navbar />
      <main>
        <div>
          <FirstRound gameStartDate={gameStartDate} currentGameCount={currentGameCount} />
         
        </div>
        <div>
          <Counter connectToAccount={connectToAccount} title="tickets" word="left" number={ticketLeft} />
          <Counter connectToAccount={connectToAccount} title="tickets in" word="draw" number={ticketInDraw} />
          <Tickets className="tickets"  connected={connected} playerRefferalCode={playerRefferalCode} RefferalLink={RefferalLink} mainLoading={mainLoading} playerTickets={playerTickets} buyTicket={buyTicket}  />
          {connected
            ?
            <div className="">
              <Button className="connect">
                Connected
              </Button>
            </ div>
            :
            <Button className="connect" onClick={() => setModal(true)}>
              Connect
            </Button>
          }
          <Button outline to="/history">
            Winner History
          </Button>
        </div>
        <div>
          <Tickets  connected={connected} playerRefferalCode={playerRefferalCode} RefferalLink={RefferalLink} mainLoading={mainLoading} playerTickets={playerTickets} buyTicket={buyTicket} />
        </div>
      </main>
      {modal && <Wallets hideModal={setModal} />}
    </>
  );
};

export default Home;
