import React, { useContext, useEffect, useState } from "react";
import styled from "styled-components";

import { Template as Tm, BackHome } from "../layout/Template";

import { ReactComponent as Winner } from "../img/winner.svg";
import { ReactComponent as LoadingIcon } from "../img/icons/loading.svg";


import { AppContext } from "../AppContext";




const History = (props) => {
  const { lotteryContract } = props;

  const { connected } = useContext(AppContext);

  const [last30Winners, setLast30Winners] = useState([]);
  const [loadingWinner, setLoadingWinner] = useState(true)

  useEffect(() => {
    if (connected) {
      loadWinnerHistory();
    
  }
}, []);

useEffect(() => {
  if (connected) {
      loadWinnerHistory();
    }
  }, [lotteryContract]);

  function timeConverter(UNIX_timestamp) {
    var a = new Date(UNIX_timestamp * 1000);
    var months = ['Jan', 'Feb', 'Mar', 'Apr', 'May', 'Jun', 'Jul', 'Aug', 'Sep', 'Oct', 'Nov', 'Dec'];
    var year = a.getFullYear();
    var month = months[a.getMonth()];
    var date = a.getDate();
    var hour = a.getHours();
    var min = a.getMinutes();
    var sec = a.getSeconds();
    var time = date + ' ' + month + ' ' + year + ' ' + hour + ':' + min + ':' + sec;
    return time;
  }


  const loadWinnerHistory = async () => {
    const getCurrentGameCount = await lotteryContract.methods.getCurrentGameCount().call();
    const _reward = await lotteryContract.methods.getOneWinnerReward().call();
    let rewardForOne = _reward.toString();

    let _latestGame = getCurrentGameCount.toString() - 1;
    let _lastGame = 1;

    if (_latestGame > 30) {
      _lastGame = _latestGame - 30;
    } else {
      _lastGame = 1;
    }

    let _winnerArray = [];
    for (let i = _latestGame; i >= _lastGame; i--) {
      let _winners = await lotteryContract.methods.getWinnerPlayerOfGame(i).call();

      let _winnerItem1 = {
        'gameCount': i,
        "winner": _winners.Player1,
        'ticket': _winners.TotalTickets,
        'reward': rewardForOne,
        'time': _winners.Time
      }
      let _winnerItem2 = {
        'gameCount': i,
        'winner': _winners.Player2,
        'ticket': _winners.TotalTickets,
        'reward': rewardForOne,
        'time': _winners.Time
      }

      _winnerArray.push(_winnerItem1);
      _winnerArray.push(_winnerItem2);
    }

    setLast30Winners(_winnerArray);
    setLoadingWinner(false)

  }

  return (
    <Template>
      <h1 data-aos="zoom-in">
        <Winner />
        Winner History
      </h1>
      {loadingWinner
        ?
        <TicketsStyle>
          <div className="loading show">
            <LoadingIcon />
          {!connected&& 
          <p>Please connect Wallet</p>
          }
          </div>
        </TicketsStyle>
        :
        <div className="table" data-aos="fade-in">
          <Table last30Winners={last30Winners} timeConverter={timeConverter} ></Table>
        </div>
      }

      <BackHome />
    </Template>
  );
};

export default History;

const Template = styled(Tm)`
  h1 {
    display: flex;
    flex-direction: column;
    align-items: center;
    text-shadow: 0px 0px 3px rgba(255, 245, 0, 0.86), 0px 0px 72px rgba(255, 245, 7, 0.43);
    color: #fffca3;
    font-family: "Fira Sans Bold", sans-serif;
    font-weight: bold;
    font-size: 48px;
    margin-bottom: 90px;
    svg {
      margin-bottom: 23px;
    }
  }
  a {
    background: linear-gradient(0deg, rgba(255, 255, 255, 0.8), rgba(255, 255, 255, 0.8)),
      linear-gradient(180deg, #ffffff 0%, rgba(255, 255, 255, 0) 100%);
    box-shadow: 0px 0px 17.0303px rgba(169, 0, 212, 0.77), 0px 0px 65.9298px rgba(180, 75, 206, 0.3),
      inset 0px 0px 6.59298px rgba(180, 75, 206, 0.3);
    bottom: 50px;
  }
  .table {
    overflow-x: auto;
  }
`;

const Table = ({ last30Winners, timeConverter }) => {
  return (
    <TableStyle>
      <thead>
        <tr>
          <th>Winner Address</th>
          <th>Date</th>
          <th>Tickets in Draw</th>
          <th>Prize</th>
        </tr>
      </thead>
      <tbody>
        {last30Winners && last30Winners.map((winner, key) => (
          <tr key={key}>
            <td>{winner.winner}</td>
            <td>{timeConverter(winner.time.toString())}</td>
            <td>{winner.ticket.toString()}</td>
            <td>{`${winner.reward / 10 ** 18} Matic`}</td>
          </tr>
        ))}
      </tbody>
    </TableStyle>

  );
};

const TableStyle = styled.table`
  width: 1300px;
  min-width: 1300px;
  border-spacing: 0;
  overflow: hidden;
  text-align: center;
  margin: 0 auto;

  td,
  th {
    padding: 20px;
  }

  thead {
    text-transform: capitalize;
    font-family: "Fira Sans Bold", sans-serif;
    font-weight: bold;
    font-size: 30px;
  }
  td,
  th {
    :last-child {
      padding-right: 80px;
    }
  }

  tbody {
    tr {
      background: #2b2b2b;
      transition: background-color 100ms;
      td {
        border-bottom: 2px solid rgba(244, 244, 244, 0.3);
      }
      :hover {
        background: #444444;
        transition: background-color 200ms;
      }
      :first-child {
        border-radius: 36px;
        td:first-child {
          border-top-left-radius: 36px;
        }
        td:last-child {
          border-top-right-radius: 36px;
        }
      }
      :last-child {
        td {
          border-bottom: none;
        }
        border-radius: 36px;
        td:first-child {
          border-bottom-left-radius: 36px;
        }
        td:last-child {
          border-bottom-right-radius: 36px;
        }
      }
    }
  }

  @media (max-width: 768px) {
    td,
    th {
      padding: 15px 20px;
    }
    padding-right: 20px;
    padding-left: 20px;
  }
`;
const TicketsStyle = styled.div`
  display: inline-block;
  .loading {
    text-align: center;
    margin-top: clamp(15px, 2.5vh, 3vh);
    display: none;

    &.show {
      display: block;
    }

    svg {
      width: 60px;
      height: 60px;
      animation: loading 1s infinite linear;
    }
  }

  @keyframes loading {
    0% {
      transform: rotate(0deg);
    }
    100% {
      transform: rotate(360deg);
    }
  }
`;