import React, { useEffect, useState } from "react";
import styled from "styled-components";

import { ModalBackground } from "../components/modals/ModalBackground";

import { ReactComponent as CalendarIcon } from "../img/icons/calendar.svg";
import { ReactComponent as ArrowBottomIcon } from "../img/icons/arrow-bottom.svg";
import ArrowsLeft from "../img/icons/arrows-left.svg";
import Illustration from "../img/icons/illustration.svg";
import SidebarBg from "../img/backgrounds/sidebar.svg";

const FirstRound = (props) => {
  const { gameStartDate, currentGameCount,className} = props;
  const [isOpen, setIsOpen] = useState(false);

  const [showTimer, setShowTimer] = useState(false)
  const [timerDays, setTimerDays] = useState();
  const [timerHours, setTimerHours] = useState();
  const [timerMinutes, setTimerMinutes] = useState();
  const [timerSeconds, setTimerSeconds] = useState();
  let interval;

  const startTimer = () => {

    const countDownDate = gameStartDate + "000";


    interval = setInterval(() => {
      const now = new Date().getTime();

      const distance = countDownDate - now;

      const days = Math.floor(distance / (24 * 60 * 60 * 1000));
      const hours = Math.floor(
        (distance % (24 * 60 * 60 * 1000)) / (1000 * 60 * 60)
      );
      const minutes = Math.floor((distance % (60 * 60 * 1000)) / (1000 * 60));
      const seconds = Math.floor((distance % (60 * 1000)) / 1000);

      if (distance < 0) {
        // Stop Timer
        clearInterval(interval);
        setShowTimer(false)

      } else {
        setShowTimer(true)
        // Update Timer
        setTimerDays(days);
        setTimerHours(hours);
        setTimerMinutes(minutes);
        setTimerSeconds(seconds);
      }
    });
  };

  useEffect(() => {
    startTimer();
  })
  function timeConverter(UNIX_timestamp) {
    var a = new Date(UNIX_timestamp * 1000);
    var months = ['Jan', 'Feb', 'Mar', 'Apr', 'May', 'Jun', 'Jul', 'Aug', 'Sep', 'Oct', 'Nov', 'Dec'];
    var year = a.getFullYear();
    var month = months[a.getMonth()];
    var date = a.getDate();
    var hour = a.getHours();
    var min = a.getMinutes();
    var time = date + ' ' + month + ' ' + year + ' ' + hour + ':' + min;
    return time;
  }

  return (
    < >
      <FirstRoundStyle className={isOpen ? "show" : ""}>
        <h2>Welcome to Player's Edge Dapp</h2>
        {showTimer
          ?
          <>
            <label >
              <CalendarIcon />
              {timeConverter(gameStartDate)}
            </label>
            <div className="box">
              <div>
                <span>{timerDays}</span>
                <span>d</span>
              </div>
              <div>
                <span>{timerHours}</span>
                <span>h</span>
              </div>
              <div>
                <span>{timerMinutes}</span>
                <span>m</span>
              </div>
              <div>
                <span>{timerSeconds}</span>
                <span>s</span>
              </div>
            </div>
          </>
          :
          <div className="footer">
            <h3>First time Visitors
            </h3>
            <span>Please check out the How to Play</span>

          </div>
        }

        <div className="footer">
          {showTimer 
            ?
            <>
              <ArrowBottomIcon />
              <h3>
                550 Early Birds will get 1 ticket for <span>FREE</span>
              </h3>
            </>
            :
            <>
                {currentGameCount == 1
                ?
                <>
              <ArrowBottomIcon style={{marginTop:40}}/>
                <h3 style={{marginTop:40}}>
                550 Early Birds will get 1 ticket for <span>FREE</span>
              </h3>
              </>
                :
              <h3>
                After each Round 50% of Ticket buyers will be chosen and get a Free Ticket as soon they buy 1 ticket in new Round!
              </h3>
              }
                <h2 style={{marginTop:20}}>Good Luck !</h2>
            </>
          }
        </div>
      </FirstRoundStyle>
      {/* <OpenSidebar onClick={() => setIsOpen(true)} className={isOpen ? "hide" : ""}>
        <div  >
          <span>{currentGameCount}</span>
          <span>Round</span>
        </div>
      </OpenSidebar> */}
      {isOpen && <ModalBackground onClick={() => setIsOpen(false)} />}
    </>
  );
};

export default FirstRound;

